import React, { useState, useEffect } from 'react';
import QRScanner from './components/QRScanner';
import Quiz from './components/Quiz';
import './components/App.css';

const App = () => {
  const [scannedData, setScannedData] = useState(null);
  const [existingData, setExistingData] = useState([]);
  const [showScanner, setShowScanner] = useState(false); // QRScanner
  const [inputValue, setInputValue] = useState(''); // 
  const [showInput, setShowInput] = useState(false); // 

  useEffect(() => {
    const savedData = localStorage.getItem('quizData');
    if (savedData) {
      setExistingData(JSON.parse(savedData));
    }
  }, []);

  const handleDataReceived = (data) => {
    try {
      console.log("parsed data:", data);
      const newQuestionId = data.question[0].id;

      // id
      const idExists = existingData.some(item => item.question[0].id === newQuestionId);

      if (!idExists) {
        // 
        const updateData = [...existingData, data];

        // 
        localStorage.setItem('quizData', JSON.stringify(updateData));
        setScannedData(data);
      } else {
        alert("");
        console.log("id already exists");
      }

    } catch (err) {
      console.error("json parse error:", err);
      alert("QR");
    }
    setShowScanner(false); // QRScanner
  };

  const handleQRSwitch = () => {
    setShowScanner(prevState => !prevState); // QRScanner
  };

  const handleInputChange = (event) => {
    setInputValue(event.target.value);
  };

  const handleClearLocalStorage = () => {
    if (inputValue === 'get') { // 
      localStorage.removeItem('quizData');
      localStorage.removeItem('correctQuestionIds');
      setScannedData(null);
      setExistingData([]);
      alert('');
    } else {
      alert('');
    }
  };

  const handleShowInput = () => {
    setShowInput(true); // 
  };

  return (
    <div className="container">
      <h1>QR</h1>
      <button className="scannerbutton" onClick={handleQRSwitch}>
        {showScanner ? '' : 'QR'}
      </button>
      {showScanner && <QRScanner onDataReceived={handleDataReceived} />}
      {scannedData && <Quiz data={scannedData} />}
      {showInput ? (
        <>
          <input
            type="text"
            value={inputValue}
            onChange={handleInputChange}
            placeholder="K1-815"
          />
          <button className="exchangebutton" onClick={handleClearLocalStorage}>!</button>
        </>
      ) : (
        <button className="exchangebutton" onClick={handleShowInput}></button>
      )}
    </div>
  );
};

export default App;