import React, { useEffect, useState } from 'react';
import './App.css';

const Quiz = ({ data }) => {
  const [currentQuestionIndex, setCurrentQuestionIndex] = useState(0);
  const [selectedAnswer, setSelectedAnswer] = useState('');
  const [correctQuestionIds, setCorrectQuestionIds] = useState([]);
  const [showScore, setShowScore] = useState(false);

  const questions = data.question; // dataquestions

  useEffect(() => {
    // 
    setCurrentQuestionIndex(0);
    setSelectedAnswer('');
    setShowScore(false);

    // ID
    const savedCorrectQuestionIds = localStorage.getItem('correctQuestionIds');
    if (savedCorrectQuestionIds !== null) {
      setCorrectQuestionIds(JSON.parse(savedCorrectQuestionIds));
    }
  }, [data]); // data

  useEffect(() => {
    // ID
    localStorage.setItem('correctQuestionIds', JSON.stringify(correctQuestionIds));
  }, [correctQuestionIds]);

  if (!questions || questions.length === 0) {
    return <div></div>;
  }

  const currentQuestion = questions[currentQuestionIndex];

  console.log('Current Question:', currentQuestion);

  const handleAnswerSelect = (event) => {
    setSelectedAnswer(event.target.value);
  };

  const handleSubmit = () => {
    if (selectedAnswer === currentQuestion.answer) {
      // IDID
      if (!correctQuestionIds.includes(currentQuestion.id)) {
        alert("!!");
        setCorrectQuestionIds([...correctQuestionIds, currentQuestion.id]);
      } else if (correctQuestionIds.includes(currentQuestion.id)) {
        alert("!");
      }
    }
    const nextQuestionIndex = currentQuestionIndex + 1;
    if (nextQuestionIndex < questions.length) {
      setCurrentQuestionIndex(nextQuestionIndex);
      setSelectedAnswer('');
    } else {
      setShowScore(true);
    }
    console.log('Next Question Index:', nextQuestionIndex);
  };

  const score = correctQuestionIds.length; // ID

  return (
    <div>
      {showScore ? (
        <div className="score">{score}</div>
      ) : (
        <div>
          <div className="quiz-question">{currentQuestion.question}</div>
          <ul className="quiz-options">
            {currentQuestion.options && currentQuestion.options.map((option, index) => (
              <li key={index}>
                <input
                  type="radio"
                  id={`option-${index}`}
                  value={option}
                  checked={selectedAnswer === option}
                  onChange={handleAnswerSelect}
                />
                <label htmlFor={`option-${index}`}>{option}</label>
              </li>
            ))}
          </ul>
          <button className="answerbutton" onClick={handleSubmit}></button>
        </div>
      )}
    </div>
  );
};

export default Quiz;